import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

import user from '@/components/user'
import dataView from '@/components/dataView'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'index',
    component: index
  },
  {
    path: '/user',
    name: 'user',
    component: user
  },
  {
    path: '/:type/:id/view',
    name: 'dataView',
    component: dataView
  }
  ]
})



